/* ----------------------------------------
Sass Modules
---------------------------------------- */
@import 'variables', 'lists', 'mixins';

/* ----------------------------------------
Base Styles
---------------------------------------- */
@import 'base';

body .scope { // Control Salesforce1 overrides
    /* ----------------------------------------
    Grid Styles
    ---------------------------------------- */
    @import 'grids';
    
    /* ----------------------------------------
    Salesforce1 Styles
    ---------------------------------------- */
    @import 'salesforce1/typography', 'salesforce1/layout', 'salesforce1/imagery', 'salesforce1/interactive', 'salesforce1/forms';
    
    /* ----------------------------------------
    App Styles
    ---------------------------------------- */
    @import 'app';
}

body, body .scope { // Plugin elements can dynamically appear outside of scope
    /* ----------------------------------------
    Overrides
    ---------------------------------------- */
    @import 'overrides/tinymce', 'overrides/tag-it', 'overrides/tooltipster', 'overrides/jquery-ui';
}

body #scope { // Override all the things
    /* ----------------------------------------
    Adjustment Styles
    ---------------------------------------- */
    @import 'adjustments';
}